import React, { Suspense } from "react";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { Model } from "./Model";

const CanvasObject = React.forwardRef(({ item, currentColor, changeOnLoaded }, ref) => {
  return (
    <Suspense fallback={<div className="loading">Загрузка&#8230;</div>}>
      <Canvas
        resize={{ scroll: false }}
        style={{ height: "140px" }}
        camera={{
          fov: 10,
          near: 0.1,
          far: 5000,
          position: item.cameraPosition,
          rotation: item.cameraRotation,
        }}>
        <ambientLight intensity={0.75} />
        <rectAreaLight
          width={400}
          height={400}
          intensity={0.75}
          color={"white"}
          position={[-165, 100, 0]}
          rotation={[0, 180, 0]}
          castShadow
        />
        <Model
          model={item.model}
          color={currentColor.hex}
          scale={item.scale ? item.scale : [0.1, 0.1, 0.1]}
          rotation={item.rotation}
          changeOnLoaded={changeOnLoaded}
        />
        <OrbitControls ref={ref} />
      </Canvas>
    </Suspense>
  );
});
export default React.memo(CanvasObject);
