import React from "react";
import { useGLTF } from "@react-three/drei";
import { useLoader } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";

export const Model = React.memo((props) => {
  const url = `./assets/models/${props.model}`;

  // const { nodes, materials } = useGLTF(url);

  const model = useLoader(GLTFLoader, url);

  // const model = useLoader(GLTFLoader, url, (loader) => {
  //   const dracoLoader = new DRACOLoader();
  //   dracoLoader.setDecoderPath("/examples/jsm/libs/draco/");
  //   loader.setDRACOLoader(dracoLoader);
  // });

  const { nodes, materials } = model;

  const memoNodes = React.useMemo(() => nodes, [nodes]);
  const memoMaterials = React.useMemo(() => materials, [materials]);

  return (
    <group {...props} dispose={null}>
      <group dispose={null}>
        <mesh geometry={memoNodes["w1ll-k"].geometry} material={memoMaterials.k}>
          <meshStandardMaterial color={props.color} />
        </mesh>
        <mesh geometry={memoNodes["w1ll-b"].geometry} material={memoMaterials.b}>
          <meshStandardMaterial color={"#fff"} />
        </mesh>
      </group>
    </group>
  );
});
